<template>
  <div class="survey_info">
    <van-button icon="success" type="primary" :disabled="[0,1].indexOf(value)>-1 && disable"
      v-show="[0,1].indexOf(value)===-1 || value===1" @click="clickSurvey(true)">
      已掌握
    </van-button>
    <van-button icon="cross" type="danger" :disabled="[0,1].indexOf(value)>-1 && disable "
      v-show="[0,1].indexOf(value)===-1  || value===0" @click="clickSurvey(false)">
      未掌握
    </van-button>
  </div>
</template>
<script>
export default {
  components: {},
  props: {
    master: {
      type: Number,
      default: null
    },
    // 可编辑状态，默认不能编辑
    disable: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      value: this.master
    };
  },
  created() {},
  methods: {
    clickSurvey(val) {
      this.$emit("selectSurvey", val);
    },
    resetVal(val) {
      this.value = val;
    }
  },
  watch: {
    master(newVal, oldVal) {
      this.value = newVal;
    }
  }
};
</script>

<style lang='less' scoped>
.survey_info {
  display: flex;
  justify-content: center;

  width: 100vw;
  padding: 2vh 0;
  background: #ffffff;
  button {
    width: 50%;
    margin: 0 10vw;
  }
}
</style>


