<template>
  <div class="r-image-text" v-if="dataList.length > 0">
    <div v-for="(item, index) in dataList" :key="index" class="item">
      <r-image :ref="'refImg' + index" width="90vw" height="60vw" :src="item.img"></r-image>
      <div class="item_time">
        <div class="info">{{ item["time"] | formatDate(item["time"]) }}</div>
      </div>
    </div>
    <div class="list_end">
      再也没有了~
    </div>
  </div>
</template>
<script>
import rImage from "./r-image";

export default {
  name: "r-image-text",
  components: { rImage },
  props: {
    dataList: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {};
  },
  methods: {
    autoShow() {
      if (this.$store.getters.boardNote && this.$store.getters.boardNote.id) {
        let obj = this.dataList.find(
          r => r.id === this.$store.getters.boardNote.id
        );

        if (obj) {
          setTimeout(() => {
            let imgDom = this.$refs["refImg0"][0];
            imgDom.preview();
            this.$store.commit("SET_BOARDNOTE", {});
          }, 500);
        }
      }
    }
  }
};
</script>

<style lang="less" scoped>
@import url("./../../styles/common/base.less");
.r-image-text {
  width: 100vw;
  background: #ebf0ef;
  .item {
    margin: 10px 10px 0;
    padding: 10px 0;
    overflow: hidden;
    background-color: #fff;
    .item_time {
      margin-top: 10px;
      font-size: @font-title;
      color: #888;
    }
  }

  .list_end {
    text-align: center;
    padding: 10px;
    color: #2c3e50;
    font-size: @font-info;
  }
}
</style>


