<template>
  <div class="r-note-view">
    <div class="title">
      <span class="title-head">{{noteItem.title || '笔记标题'}}</span>
      <div class="title-mark">{{ noteItem.classHourName}}【{{noteItem.courseName}}】</div>
      <div class="sub-title">
        <span>{{noteItem.createTime | formatDate(noteItem.createTime) }}</span>
        <van-button plain size="small" type="info" v-if="hasOperate" @click="handleDelete" :style="{border:'none'}">删除
        </van-button>
        <van-button plain size="small" type="info" v-if="hasOperate" @click="handleEdit" :style="{border:'none'}">编辑
        </van-button>
      </div>
    </div>
    <r-quill-editor ref="REditor" :funType="funType" :content="noteItem.content" :is-class="isClass"
      :is-edit="false" :max-height="maxHeight">
    </r-quill-editor>
  </div>
</template>>
<script>
import RQuillEditor from "@/components/r-quill-editor";
import { ModulePicture } from "@/utils/enum";
export default {
  name: "note-view",
  components: { RQuillEditor },
  props: {
    hasOperate: {
      type: Boolean,
      default: true
    },
       isClass: {
      //是否课中
      type: Boolean,
      default: true
    },
    noteItem: {
      type: Object,
      default: () => {
        return {
          courseID: "",
          courseName: "",
          title: "",
          content: "",
          createTime: ""
        };
      }
    }
  },
  data() {
    return {
      funType: ModulePicture.StudentNote,
      maxHeight: null
    };
  },
  mounted() {
    this.$nextTick(() => {
      // 更新高度
      setTimeout(() => {
        let title = document.getElementsByClassName("tt");
        if (title[0]) title = title[0];
        this.maxHeight =
          "calc(100vh - 45px - " +
          (title ? title.clientHeight - 2 : 0) +
          "px)";
      }, 500);
    });
  },
  methods: {
    handleDelete() {
      this.$emit("toDelete");
    },
    handleEdit() {
      this.$emit("toEdit");
    }
  }
};
</script>>



