<template>
  <div class="r-note-list">
    <note-item v-for="(item, index) in dataList" :key="index" :note-item="item"
      @select="handleSelect">
    </note-item>
  </div>
</template>>
<script>
import NoteItem from "./r-note-item";
export default {
  name: "note-list",
  components: { NoteItem },
  props: {
    dataList: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {};
  },
  methods: {
    handleSelect(item) {
      this.$emit("select", item);
    }
  }
};
</script>>



